:root {
    /* --navigation: rgba(255, 255, 255, 0.1); */
    --boldName: rgb(47, 148, 243);
    --linkBar: rgba(241, 210, 28, 0.527); 
    --menu-slide: rgba(53, 48, 49, 1.911);
    --buttons: rgba(144, 42, 228, 0.7);
    --description: rgba(238, 231, 248, 0.6);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
body {
  font-family: 'Sofia Sans Semi Condensed', sans-serif;
  background: linear-gradient(black, gray);
}

button {
  cursor: pointer;
}

.app-container {
  /* display: flex; */
  justify-content: center;
}

/* ==================================Navigation========================== */
.nav-container {
  background-color: var(--navigation);
  min-width: 100%;
  height: 60px;
  font-size: larger;
  position: fixed;
  top: 0;
  z-index: 1;
}

.logo {
  position: fixed;
  height: 47px;
  margin: 0 auto 0 80%;
  z-index: 11;
}

.ham-bars {
  position: fixed;
  height: 25px;
  top: 17px;
  left: 25px;
  z-index: 92;
}

.menu-open {
  position: relative;
  background-color: var(--menu-slide);
  height: 100vh;
  width: 100%;
  z-index: 11;
}

.menu-close {
  position: fixed;
  background-color: var(--menu-slide);
  border: none;
  height: 0vh;
  width: 100%;
  z-index: -1;
}

.transition {
  transition: all 250ms ease;
}

.li-display {
  position: relative;
  top: -20em;
  width: 100%;
  flex-direction: column;
  list-style-type: none;
  font-weight: 600;
  font-size: larger;
  text-transform: uppercase;
  z-index: 12;
  text-align: center;
}

.li-none {
  display: none;
  margin-top: 15%;
}

.menu-link {
  margin-top: 3em;
}
/*===================================Header=====================================*/
.header {
  background-image: url("/Users/lasky/Downloads/shahadat-rahman-BfrQnKBulYQ-unsplash.jpg");
  width: 100%;
  background-repeat: no-repeat;
  background-size: cover;
}

.header-container {
  max-width: 500px;
  color: white;
  padding: 5em 2em;
  margin: 0 auto;
}

.header-container > h1, h3, h4 {
  margin-top: 15px;
}

.bold-name {
  color: var(--boldName);
  font-weight: bolder;
}


a {
  text-decoration: none;
  color: inherit;
}

a:visited {
  color: none;
}

a:hover {
  text-decoration: none;
}

.media-links{
  display: flex;
  justify-content: center;
  justify-content: space-around;
  background-color: var(--linkBar);
  border-radius: 4px;
  padding: 5px;
  margin-top: 2em;
}

.media-links a {
  display: flex;
}

.header-workBtn {
  background-color: var(--boldName);
  border-radius: 7px;
  border: none;
  color: rgb(0, 0, 0);
  padding: 1em;
  font-size: 1.01rem;
  display: flex;
  margin: 1em auto;
}

/*===============================Projects========================*/
/* copied code */
.fade-in-section {
  opacity: 0;
  transform: translateY(20vh);
  visibility: hidden;
  transition: opacity 0.6s ease-out, transform 1.2s ease-out;
  will-change: opacity, visibility;
}
.fade-in-section.is-visible {
  opacity: 1;
  transform: none;
  visibility: visible;
}
/* end copied code */

.project-frame {
  display: inline-block;
  margin: 25% auto 0 auto;
  animation: fade-in none;
  width: 100%;
}
.project-frame {
  /* animation: fadeIn ease 2s;
  -webkit-animation: fadeIn ease 2s;
  -moz-animation: fadeIn ease 2s;
  -o-animation: fadeIn ease 2s;
  -ms-animation: fadeIn ease 2s; */
}
@keyframes fadeIn {
  0% {
    opacity:0%;
  }
  100% {
    opacity:100%;
  }
}
@-moz-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
@-o-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}
@-ms-keyframes fadeIn {
  0% {
    opacity:0;
  }
  100% {
    opacity:1;
  }
}

.image-section-container {
  display: flex;
  width: 100%;
  margin: 0 auto;
}

.image-container {
  width: 100%;
  display: flex;
  flex-direction: column;
}

.project-img {
  width: 300px;
  object-fit: contain;
  transition: opacity 0.5s ease;
  margin: 0 auto;
}

.responsive-demo-btn-container {
  position: relative;
  top: -50px;
  max-width: 250px;
  margin: 0 auto;
}

.responsive-demo-buttons {
  border-radius: 6px;
  border: none;
  margin-left: 1em;
  padding: 10px;
  /* background-color: orange; */
}

.project-dscrp {
  display: none;
  /* max-width: 300px; */
}

.description-responsive {
  /* color: orange; */
  font-size: larger;
  font-weight: bold;
  text-align: center;
  margin-top: -2em;
  padding: 2em;
}
