/* =========================Desktop Responsive========================= */

@media (min-width: 800px) {
  .ham-bars {
    display: none;
  }

  .menu-open {
    display: none;
  }

  .logo {
    position: fixed;
    left:-70%;
    width:190px;
    object-fit: cover;
  }
 
  .li-display {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    position: fixed;
    top: 0;
  }

  .li-none {
    display: flex;
    justify-content: flex-end;
    list-style: none;
    font-weight: 900;
    text-transform: uppercase;
    margin: 0 auto;
  }

  .menu-link {
    margin: .75em 3em 0.75em 1em;
    font-size: 1.25rem;
    font-weight: 100;
    padding: 5px 5px 5px 5px;
    color: aliceblue;
  }

  .menu-link:active {
    color: inherit;
  }

  .menu-link:hover {
    background-color: rgba(192, 161, 161, 0.7);
    border-radius: 4px;
  }

/*===================================Header================================*/
  .header-container {
    max-width: 1000px;
    color: white;
    font-size: 1.4rem;
    animation: left-to-right 400ms ease-in;
  }

  @keyframes left-to-right {
    0% {
      transform: translatex(-100%)
    }
  }
/* ===================Projects====================== */

  .project-frame {
    margin-top: 2.5em;
  }

  .image-section-container {
    display: flex;
  }

  .image-container {
    display: flex;
    flex-direction: row;
    margin: 5em 1.5em;
  }

  .project-img {
