  .project-dscrp {
    display: block;
    max-width: 300px;
    margin: 10em 5em 0 auto;
    font-size: larger;
    font-weight: lighter;
    color: var(--description);
  }

  .display-text-btn {
    cursor: pointer;
    background: none;
    padding: 5px;
    border: none;
    color: rgb(47, 190, 247);
  }

  .demo-buttons-container {
    display: flex;
    justify-content: center;
    margin-top: 35px;
  }

  .demo-buttons-container button {
    margin-left: 3em;
    border: none;
    padding: 15px;
    border-radius: 10px;
    background-color: var(--buttons);
    box-shadow: 0px 0px 5px 2px black;
    color: aliceblue;
  }

  .demo-buttons:hover {
    box-shadow: 0px 0px 1px 1px white;
  } 
  .demo-buttons:active {
    box-shadow: 0px 0px 5px white;
  }
  
  /* ===============Contact===================== */

  input[type=text],input[type=email], textarea {
    width: 300px;
    resize: none;
  }

  .submit {
    display: flex;
    justify-content: center;
    font-weight: 700;
    font-size: large;
    width: 100%;
  }
}